import { login, logout, getUserInfo } from '@/api/user'
import { getToken, setToken, removeToken } from '@/utils/token'

const user = {
  state: {
    token: getToken(),
    userId: -1,
    email: null,
    username: null,
    avatar: null,
    roleName: null,
    permissionCodeList: []
  },

  mutations: {
    SET_TOKEN: (state, token) => {
      state.token = token
    },
    SET_USER: (state, user) => {
      state.userId = user.userId
      state.email = user.email
      state.username = user.username
      state.avatar = 'https://wpimg.wallstcn.com/f778738c-e4f8-4870-b634-56703b4acafe.gif'
      state.roleName = user.roleName
      state.permissionCodeList = user.permissionCodeList
    },
    RESET_USER: (state) => {
      state.token = null
      state.userId = -1
      state.email = null
      state.username = null
      state.avatar = null
      state.roleName = null
      state.permissionCodeList = []
    }
  },

  actions: {
    // 
    Login({ commit }, user) {
      return new Promise((resolve, reject) => {
        login(user).then(response => {
          if (response.returnCode === 200) {
            // cookietoken
            setToken(response.data)
            // vuextoken
            commit('SET_TOKEN', response.data)
          }
          // /login/index.web : store.dispatch('Login').then(data)
          resolve(response)
        }).catch(error => {
          reject(error)
        });
      })
    },

    // 
    GetUserInfo({ commit }) {
      return new Promise((resolve, reject) => {
        getUserInfo().then(response => {
          // 
          commit('SET_USER', response.data)
          resolve(response)
        }).catch(error => {
          reject(error)
        })
      })
    },

    // 
    Logout({ commit }) {
      return new Promise((resolve, reject) => {
        logout().then(() => {
          // token
          commit('RESET_USER')
          removeToken()
          resolve()
        }).catch(error => {
          reject(error)
        })
      })
    },

    //  
    FedLogout({ commit }) {
      return new Promise(resolve => {
        commit('RESET_USER')
        removeToken()
        resolve()
      })
    }
  }
}

export default user
